const texthide = ['', '']
const imgnodes = {
    andCondition,
    andOr,
    and,
    banCondition,
    ban,
    base,
    condition,
    not,
    or,
    result,
    switchNode,
    turn,
    unknown,
    vote,
    similar
}
export default function exportDom(param) {
    const wrap = document.createElement("div");
    const span = document.createElement("pre");
    if (!texthide.includes(param.tooltip)) span.textContent = param.tooltip;
    if (param.tooltip === '') {
        span.textContent = "(r)/(n)"
    }
    span.style.cssText = param.defaultSpanStyle || spanCss;
    const imgEl = document.createElement('img')
    imgEl.src = imgnodes[param.urlStr]
    imgEl.style.cssText = "width: 100%;height: 100%;"
    wrap.setAttribute("class", 'nodeBox')
    wrap.appendChild(imgEl)
    wrap.appendChild(span);
    wrap.style.cssText = 'width: 100%;height: 100%;display: flex;align-items: center;justify-content: center;border-radius: 4px;';
    return wrap;
}