import { Graph } from "@antv/x6";
import removeBtn from './removeBtn'
import { ToolTypeEnum } from './enums'
/**
 * x6
 * @returns {graph} Graph
 */
export function initGraph() {
    const graph = new Graph({
        container: document.getElementById("container"),
        keyboard: {
          enabled: true,
          global: true,
        },
        autoResize: true,
        resizing: {
          preserveAspectRatio: true,
          enabled: true,
        },
        // /
        selecting: {
          enabled: true,
          showNodeSelectionBox: false,
          // https://x6.antv.vision/zh/docs/tutorial/basic/selection
          rubberband: true, // 
          modifiers: "ctrl", // 
        },
        // 
        snapline: true,
        // 
        // https://x6.antv.vision/zh/docs/tutorial/basic/graph/#%E7%94%BB%E5%B8%83%E5%B9%B3%E7%A7%BB
        panning: {
          enabled: true,
          eventTypes: ["leftMouseDown", "rightMouseDown", "mouseWheel"],
        },
        // 
        grid: {
          size: 10, //  10px
          visible: true, // 
        },
        //  Clipboard
        // https://x6.antv.vision/zh/docs/tutorial/basic/clipboard
        clipboard: true,
        // / Redo/Undo
        // https://x6.antv.vision/zh/docs/tutorial/basic/history/#gatsby-focus-wrapper
        history: {
          enabled: true,
          // ignoreAdd: true,
          // ignoreRemove: false,
          // ignoreChange: false,
          beforeAddCommand(event, args) {
            if (args.key) {
              //  Undo 
              return args.key !== 'tools' && args.key !== 'html'
            }
          },
        },
        // 
        // https://x6.antv.vision/zh/docs/tutorial/basic/interacting/#%E5%AE%9A%E5%88%B6%E4%BA%A4%E4%BA%92%E8%A1%8C%E4%B8%BA
        interacting: function (cellView) {
          if (cellView.cell.getData()?.disableMove) {
            return { nodeMovable: false };
          }
          return true;
        },
        // 
        // https://x6.antv.vision/zh/docs/api/graph/interaction
        connecting: {
          // 
          snap: true,
          // 
          allowBlank: false,
          // 
          allowLoop: false,
          // 
          allowMulti: false,
          // 
          // https://x6.antv.vision/zh/docs/tutorial/basic/interacting/#highlight
          highlight: true,
          //  sourceAnchor 
          sourceAnchor: {
            name: "center",
          },
          //  targetAnchor 
          targetAnchor: "center",
          //  boundary
          connectionPoint: "anchor",
          //   d  normal
          connector: {
            name: "rounded",
            args: {
              radius: 20,
            },
          },
          //  vertices  normal
          router: "metro",
          // https://x6.antv.vision/zh/docs/tutorial/basic/interacting/#validatemagnet
          // 
          validateMagnet({ magnet }) {
            const portGroup = magnet.getAttribute("port-group");
            return portGroup !== "in";
          },
          // 
          createEdge() {
            return this.createEdge({
              zIndex: -1,
              attrs: {
                line: {
                  // strokeDasharray: "5 5",
                  stroke: "#7c68fc",
                  strokeWidth: 2,
                  targetMarker: {
                    name: "block",
                    args: {
                      size: "6",
                    },
                  },
                },
              },
            });
          },
          getHTMLComponent(node) {
            const data = node.getData();
            if (data.flag) {
              return document.createElement("div");
            }
            return document.createElement("p");
          },
          // https://x6.antv.vision/zh/docs/tutorial/basic/interacting/#validateconnection
          //  false
          validateConnection({
            targetView,
            sourceMagnet,
            targetMagnet,
            sourceCell,
          }) {
            if (!sourceMagnet || !targetMagnet) {
              return false;
            }
            if (sourceCell.getData()?.disableMove) return false;
            // 
            const portId = targetMagnet.getAttribute("port");
            const node = targetView.cell;
            const port = node.getPort(portId);
            if (!port) {
              return false;
            }
            return true;
          },
        },
        // 
        mousewheel: {
          enabled: true,
          modifiers: ["ctrl", "meta"],
        },
      });

      Graph.registerNodeTool(ToolTypeEnum.REMOVE_BUTTON, removeBtn, true);
      Graph.registerEdgeTool(ToolTypeEnum.REMOVE_BUTTON, removeBtn, true);

    return graph
}