
import exportDom from './createDom'


/**
 * node
 * @param {*} node 
 * @returns 
 */
export function createImageNode(node) {
  const { urlStr, tooltip, ports, group, width, height, typeImgNo, y = 50, x = 120, defaultSpanStyle } = node
  let protsArr = []
  if (ports.length) {
    for (let i = 0; i < ports.length; i++) {
      let obj = {
        group: group,
        id: (group + i),
        args: ports[i].args
      }
      protsArr.push(obj)
    }
  }
  return {
    shape: "html",
    x,
    y,
    width: width,
    height: height,
    data:{
      typeImgNo,
      textContent: tooltip,
      defaultSpanStyle
    },
    ports: {
      items: protsArr,
      groups: {
        [group]: {
          attrs: {
            circle: {
              r: 6,
              magnet: true,
              stroke: '#31d0c6',
              fill: '#fff',
              strokeWidth: 2
            }
          },
          position: {
            name: 'absolute'
          }
        }
      }
    },
    html: () => exportDom({urlStr, tooltip, ports, group, width, height, typeImgNo, defaultSpanStyle})
  }
}