
//
import imgNode from './imgNode';
let nodes = []
for(let key in imgNode){
    const item = imgNode[key]
    nodes.push({
        label: item.label,
        styles: getStyles(),
        shape: 'html',
        url: require(`${item.url}`),
        urlStr: `${item.urlName}`,
        group: `${item.group}`,
        ports: item.ports,
        yAxis: item.yAxis,
        typeImgNo: key,
        width: item.width,
        height: item.height,
        defaultSpanStyle: item.defaultSpanStyle || ''
    })
}

function getStyles() {
    return {
        width: '50px',
        height: '50px',
        textAlign: 'center',
        borderRadius: '4px',
        color: '#7D7671',
    }
}

export { nodes};